<template>
  <div class="indexPage">
      <header class="top-hd">
            <div class="in-hd">
                <h1 class="title"></h1>
            </div>
        </header>
        <section class="main-content mer">
            <div class="inner-content">
      
                <div class="page-area default">
                    <div class="banner-area">
                        <!-- <img src="../images/null.gif" data-url="../images/pic1.jpg" class="img" style="display:none"> -->
                        <a href="javascript:;" class="addImgBtn" style="z-index:9">
                            <input class="imgFile" @change="uploadPic" type="file" accept="image/*">
                        </a>
                        <div class="swiper-container home-banner slide-banner">
      
                            <div class="swiper-wrapper">
      
      
                            </div>
                            <div class="swiper-pagination in-banner-dd" ></div>
      
                        </div>
      
                        <p class="intro"></p>
                    </div>
                    <div class="pub-form">
                        <div class="fm-lis fm-lis-right">
                            <div class="inner">
                                <form>
                                    <div class="area">
                                        <div class="row">
                                            <label>
                                                <input type="text" placeholder="" class="txt lfTxt" id="title" v-model="goods_tit">
                                            </label>
                                        </div>
                                        <div class="fg"></div>
                                        <div class="row">
                                            <label><em></em>
                                                <input type="text" class="txt" v-model="category" id="category" v-link="{path:'/category',exact: true }" readonly><i class="arrow"></i>
                                            </label>
                                        </div>
                                    </div>
                                    <div class="area mt">
                                        <div class="row">
                                            <label>
                                                <em><b></b></em>
                                                <input type="text" class="txt" v-model="brand" id="brand" v-link="{path:'/brand'}" readonly><i class="arrow"></i></label>
                                        </div>
                                        <div class="fg"></div>
                                        
                                    </div>
                                    <div class="area mt">
                                       
                                        <div class="row">
                                            <label>
                                                <em></em>
                                                <input type="text" class="txt" id="inventory" v-model="inventory" @keyup="chk_in"></label>
                                        </div>
                                        <div class="fg"></div>
                                        <div class="row">
                                            <label>
                                                <em><strong></strong><b>()</b></em>
                                                <input type="text" class="txt" id="sellPrice" v-model="sellPrice" @keyup="inputCheck"><i class="rmb"></i></label>
                                        </div>
                                        <div class="fg"></div>
                                        <div class="row">
                                            <label>
                                                <em><b>()</b></em>
                                                <input type="text" class="txt" id="basePrice" v-model="basePrice" @keyup="inputCheck2"><i class="rmb"></i></label>
                                        </div>
      
                                    </div>
                                  
                                    <div class="area mt">
                                        <div class="row">
                                            <label>
                                                <em></em><span v-if="des" style="color:red; text-align:right"></span>
                                                <input type="text" class="txt" v-link="{path:'/des'}" id="goods_des" readonly><i class="arrow"></i></label>
                                        </div>
                                    </div>
                                    <div class="area mt">
                                        <div class="row">
                                            <label>
                                                <em></em>
                                                <input type="text" class="txt" v-link="{path:'/city'}" v-model="city" id="place" readonly><i class="arrow"></i></label>
                                        </div>
                                    </div>

                                    <div class="area mt" style="margin-bottom:30px">
                                        <div class="row">
                                            <label>
                                                <em></em>
                                            </label>
      
                                            <div class="radio-sec">
                                                <a href="javascript:;" class="radio-item radio-cur"  style="margin-right:40px;" @click="toggleTrue" v-bind:class="{'radio-cur':day7==true}"><s></s> </a><a href="javascript:;" class="radio-item" v-bind:class="{'radio-cur':day7==false}"  @click="toggleFalse"  ><s></s> </a>
                                            </div>
      
                                        </div>
                                    </div>
      
                                </form>
                            </div>
                        </div>
      
                    </div>
                </div>
      
            </div>
        </section>
        <footer class="footer">
            <div class="cross2Btn">
                
                <a href="javascript:;" class="btnPub btn-first" @click="addBox"></a>
                <a href="javascript:;" class="btnPub btn-two" @click="addBox"></a>
            </div></footer>
  </div>
</template>

<script>
    



var swiper = require('../swiper.js');
    
require('../dialog.js');
    
//console.log(Swiper);
    
var s = null;
    
 
export default {
    
    
    data(){
    
      return {
        
          goods_tit:"",
          
          category: "",
          
          brand:"",
          
          inventory:"",
          
          des:"",
          
          city:"",
          
          sellPrice:"",
          
          basePrice:"",
          
          day7:true
      }  
    },
    
    route: {
        
      data: function(transition) {
          
          return {
           
             //secCity : transition.to.query.secCity,
              
          }
       //category=transition.to.params.sec
        // ...
      },
        
      activate: function (transition) {
         
          if(transition.to.query.sort === "category"){
            this.category = transition.to.query.sec;
          }else if(transition.to.query.sort === "brand"){
            this.brand=transition.to.query.sec;
          }else if(transition.to.query.sort === "des"){
            this.des = transition.to.query.sec
          }else if(transition.to.query.sort === "city"){
            this.city = transition.to.query.sec
          }
          
          
         transition.next();
          
          }
    },
    methods:{
    
        uploadPic:function(event,r){
            
            var _this = event.target;
            
            var _file = _this.files[0];
            
            r = new FileReader();
            
            r.readAsDataURL(_file);
            
            $(".swiper-pagination").html("");
            
            getLocalImg(_this,r);
            
        },
        toggleTrue:function(){
        
           this.day7 = true;
        },
        toggleFalse: function(){
        
            this.day7 = false;
            
         },
        
        inputCheck:function(){
        
            
            if(!/^\d{0,8}\.{0,1}(\d{1,2})?$/.test(this.sellPrice)){
            
                this.sellPrice = "";
            }
        },
        
        inputCheck2 : function(){
        
            if(!/^\d{0,8}\.{0,1}(\d{1,2})?$/.test(this.basePrice)){
            
                this.basePrice=""
            }
        },
        
        chk_in:function(){
            
            if(!/^(0|\+?[1-9][0-9]*)$/.test(this.inventory)){
            
                this.inventory=""
            }
        
        },
        
        addBox:function(){
            
            console.log($.tipBox);
            
            console.log(this.$data);
            
            for(var i in this.$data){
                
                if(this.$data.hasOwnProperty(i)){
                
                    console.log(i+"===================="+this.$data[i]);
                    
                    if(this.$data[i] == undefined || this.$data[i].length == 0){
                    
                        $.modal({
            
                            title: "",

                            content: "",

                            btnArr: ["", ""],

                            events: []
                        });return
                        
                    }else{
                    
                        
                    }
            
                }
            }
            
            $.tipBox({info:""}).appear(function(){
            
                location.reload();
            });
        }
    
    },
    
    ready:function(){
        
        s = new Swiper('.swiper-container',{
            pagination : '.swiper-pagination',
            //observer:true
        })
        
        this.$nextTick(function () {
        // DOM 
        // `this` 
        $(".swiper-pagination").html("");
            
      })
    }

}
     
    //
    
    function getLocalImg(obj,r){
        
        $(r).on("load",function(){
                
            var imgSrc = this.result;
                
            var appendHTML = '<div class="swiper-slide"><img src="' + imgSrc + '" alt=""  width="100%" height:"auto"></div>';
            
           $(".swiper-wrapper").append($(appendHTML));
            
           $(".banner-area").addClass("hasImage");
           
         s.updateSlidesSize();
        s.updatePagination();
        s.updateClasses();

                
        })
    }
    //queryString
  
</script>
<style>
    
    @import '../style/swiper.css';
    
     html, body {
        position: relative;
        height: 100%;
    }
    body {
        background: #eee;
        font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
        font-size: 14px;
        color:#000;
        margin: 0;
        padding: 0;
    }
    .swiper-container {
        width: 100%;
        height: 100%;
    }
    .swiper-slide {
        text-align: center;
        font-size: 18px;
/*        background: #fff;*/
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
    }
    
    
</style>





